import React, { ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, KeyboardEvent, ReactNode } from 'react'

import { setNewCurrnetNameAC, setNewNameAC, updateUserDataTC } from '../../../features/profile/reducerProfile'

import s from './SuperInputText.module.css'

import { useAppDispatch } from 'app/store'

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               ,  type
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = Omit<DefaultInputPropsType, 'type'> & {
  //  +       
  onChangeText?: (value: string) => void
  onEnter?: () => void
  error?: ReactNode
  spanClassName?: string
}

const SuperInputText: React.FC<SuperInputTextPropsType> = ({
  onChange,
  onChangeText,
  onKeyPress,
  onEnter,
  error,
  className,
  spanClassName,
  id,

  ...restProps //       restProps
}) => {
  const dispatch = useAppDispatch()
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    console.log(e.currentTarget.value)

    onChange?.(e) //    onChange,     ( onChange  )
    dispatch(setNewCurrnetNameAC(e.currentTarget.value))
  }

  const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
    onKeyPress?.(e)

    onEnter && //    onEnter
      e.key === 'Enter' && //     Enter
      onEnter() //   
    if (e.key === 'Enter') {
      if (e.currentTarget.value.trim()) {
        console.log(1)

        dispatch(updateUserDataTC(e.currentTarget.value.trim()))
      } else {
        ;('')
      }
    } else {
      ;('')
    }
  }

  const finalSpanClassName = s.error + (spanClassName ? ' ' + spanClassName : '')
  const finalInputClassName =
    // s.input + (error ? ' ' + s.errorInput : ' ' + s.superInput) + (className ? ' ' + s.className : '') //    

    s.input + (error ? ' ' + s.errorInput : ' ' + s.superInput) + (className ? ' ' + className : '') //    

  return (
    <div className={s.inputWrapper}>
      <input
        id={id}
        type={'text'}
        onChange={onChangeCallback}
        onKeyDown={onKeyPressCallback}
        className={finalInputClassName}
        {...restProps} //        (value   )
      />
      <span id={id ? id + '-span' : undefined} className={finalSpanClassName}>
        {error}
      </span>
    </div>
  )
}

export default SuperInputText
